import React, {Component} from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.scss';
import Iframe from 'react-iframe';
import axios from 'axios';
import { baseUrl } from '../../../baseUrl';
import { toast  } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';

class DetailsTopTabs extends Component {

    constructor() {
        super();
        this.state= {
            userName: '',
            email: '',
            comment: '',
            bookId:''
        }
    }

    setStateFromInput = (event) => {
        var obj = {};
        obj[event.target.name] = event.target.value;
        this.setState(obj);
    }

    handleSubmit = async (e) => {
        e.preventDefault();
        
        const obj = {
            email: this.state.email,
            userName: this.state.userName,
            comment: this.state.comment,
            bookId: this.props.item._id
        }

        const url = baseUrl + 'api/rating/' + this.props.item._id;
        await axios.post(url, obj)
        .then(res => {
            toast.success("Thank you for your feedback!")
        }).catch(err => {
            toast.error("Can't submit your feedback. Try agin!", err)
        })
    }

    render (){
        const {item} = this.props;
        return (
            <section className="tab-product m-0">
                <div className="row">
                    <div className="col-sm-12 col-lg-12">
                        <Tabs className="tab-content nav-material">
                            <TabList className="nav nav-tabs nav-material">
                                <Tab className="nav-item">
                                    <span className="nav-link active">
                                        <i className="icofont icofont-ui-home"></i>Description</span>
                                    <div className="material-border"></div>
                                </Tab>
                                <Tab className="nav-item">
                                    <span className="nav-link" ><i className="icofont icofont-man-in-glasses"></i>Details</span>
                                    <div className="material-border"></div>
                                </Tab>
                                <Tab className="nav-item">
                                    <span className="nav-link" >
                                        <i className="icofont icofont-contacts"></i>Video</span>
                                    <div className="material-border"></div>
                                </Tab>
                                <Tab className="nav-item">
                                    <span className="nav-link" >
                                        <i className="icofont icofont-contacts"></i>Write Review</span>
                                    <div className="material-border"></div>
                                </Tab>
                            </TabList>
                            <TabPanel className="tab-pane fade mt-4 show active">
                                <table className="table table-striped mb-0">
                                    <tbody>
                                    <tr>
                                        <th>ISBN :</th>
                                        <td>{item.isbn}</td>
                                    </tr>
                                    <tr>
                                        <th>Author :</th>
                                        {
                                            item.authorsIds.map((auth, index) =>
                                                <td key={index}>{auth.name}</td>
                                            )
                                        }
                                    </tr>
                                    <tr>
                                        <th>Category :</th>
                                        <td>{item.category}</td>
                                    </tr>
                                    <tr>
                                        <th>Pages :</th>
                                        <td>{item.pages}</td>
                                    </tr>
                                    <tr>
                                        <th>Language :</th>
                                        <td>{item.language}</td>
                                    </tr>
                                    <tr>
                                        <th>Published By :</th>
                                        <td>Naisoch Publishers</td>
                                    </tr>
                                    </tbody>
                                </table>
                            </TabPanel>
                            <TabPanel>
                                <p className="mt-4 p-0" style={{textAlign: 'justify'}}>
                                    Nai Soch Publishers is an E-commerce business. Its an emerging platform to sell the books
                                    via Web Application. The ultimate goal is to make all the published works of humankind available to everyone. 
                                    It helps readers to discover books they love while providing publisher and authors with a way to drive sales and find new fans. 
                                    Upon joining, members receive unbeatable deals selected by our team, handpicked recommendations from people they trust, 
                                    and real-time updates from their favorite authors. It has been providing books
                                    under different categories like Islamic, self help, business management, psychology etc.
                                    The authors of the books are the renowned persnalitites of Pakistan i.e. Qasim Ali Shah the most
                                    inspiring, movational speaker and kind person, has been wiriting books from last 5 years and many more.
                                    The books shape the broader cultural life of our society and inform the national conversation.
                                </p>
                            </TabPanel>
                            <TabPanel>
                                <div className="mt-4 text-center">
                                    <div className="embed-responsive embed-responsive-16by9">
                                        <Iframe
                                            url="https://www.youtube.com/embed/KHPnKnO99vA"
                                            allow="autoplay; encrypted-media"
                                            display="initial"
                                            allowFullScreen>
                                        </Iframe>
                                    </div>
                                </div>
                            </TabPanel>
                            <TabPanel>
                                <form className="theme-form mt-4" onSubmit={this.handleSubmit}>
                                    <div className="form-row">
                                        <div className="col-md-12 ">
                                            <div className="media m-0">
                                                <label>Rating</label>
                                                <div className="media-body ml-3">
                                                    <div className="rating three-star">
                                                        <i className="fa fa-star"></i>
                                                        <i className="fa fa-star"></i>
                                                        <i className="fa fa-star"></i>
                                                        <i className="fa fa-star"></i>
                                                        <i className="fa fa-star"></i>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="col-md-6">
                                            <label htmlFor="name">Name</label>
                                            <input type="text" className="form-control" id="name"
                                                placeholder="Enter Your name" required 
                                                name="userName" onChange={this.setStateFromInput}
                                                value={this.state.userName}
                                            />
                                        </div>
                                        <div className="col-md-6">
                                            <label htmlFor="email">Email</label>
                                            <input type="email" className="form-control" id="email" 
                                                placeholder="Email" required
                                                name="email" onChange={this.setStateFromInput}
                                                value={this.state.email}
                                            />
                                        </div>
                                        <div className="col-md-12">
                                            <label htmlFor="review">Comment</label>
                                            <textarea className="form-control" placeholder="Wrire Your Testimonial Here" 
                                                id="exampleFormControlTextarea1" rows="6"
                                                name="comment" onChange={this.setStateFromInput}
                                                value={this.state.comment}>
                                            </textarea>
                                        </div>
                                        <div className="col-md-12">
                                            <button className="btn btn-solid" type="submit">Submit YOur Review</button>
                                        </div>
                                    </div>
                                </form>
                            </TabPanel>
                        </Tabs>
                    </div>
                </div>
            </section>
        )
    }
}

export default DetailsTopTabs;