import React, { Component } from 'react'
import SimpleReactValidator from 'simple-react-validator';
import axios from 'axios';
import { baseUrl } from '../../baseUrl'
var Recaptcha = require('react-recaptcha');

class OrderTracking extends Component {

  constructor(props) {
    super(props);
    this.state={
      trackingId: '',
      userPhone: '', 
      order: '',
      isVerify: false
    }
    this.validator = new SimpleReactValidator();
  }

  setStateFromInput = (event) => {
    var obj = {};
    obj[event.target.name] = event.target.value;
    this.setState(obj);
  }

  handleSumbit = (e) => {
    e.preventDefault();
    let {trackingId, userPhone}  = this.state;
    axios.get(baseUrl + 'api/orders/status/' + trackingId + '/phoneNumber/' + userPhone)
    .then(res => {
      this.setState({order: res.data})
    })
  }

  recaptchaLoaded = () => {
   console.log("captcha successfully loaded!")
  }

  verifyCallback = (res) => {
    if(res) {
      this.setState({isVerify: true})
    }
  }

  render() {
    let {order} = this.state;
    
    return (
      <div className="container">
        <section className="section-b-space">
          <div className="container padding-cls">
              <div className="checkout-page">
              <div className="checkout-form">
                <form onSubmit={this.handleSumbit}>
                  <div className="checkout row">
                    <div className="col-sm-6 col-sm-12 col-xs-12">
                      <div className="checkout-title">
                        <h3>Check Your Order Status!</h3>
                      </div>
                      <div className="row check-out">
                        <div className="form-group col-md-8 col-sm-8 col-xs-8">
                          <input type="text" name="trackingId" value={this.state.trackingId} onChange={this.setStateFromInput} placeholder="Enter order tracking Id..." required="required" />
                          {this.validator.message('trackingId', this.state.trackingId, 'required')}
                        </div>

                        <div className="form-group col-md-8 col-sm-8 col-xs-8">
                          <input type="text" name="userPhone" value={this.state.userPhone} onChange={this.setStateFromInput} placeholder="Enter your mobile number" required="required" />
                          {this.validator.message('userPhone', this.state.userPhone, 'required|phone')}
                        </div>

                        <div className="form-group col-md-8 col-sm-8 col-xs-8">
                          <Recaptcha
                            sitekey="6LdWB9wZAAAAAEF1-7VhB9xl028ZK4uuURBHLBQB"
                            render="explicit"
                            onloadCallback={this.recaptchaLoaded}
                            verifyCallback={this.verifyCallback}
                          />
                        </div>
                  
                        </div>
                        
                    </div>
                  </div>
                <div className="col-sm-6 col-sm-12 col-xs-12 text-center" style={{marginTop: 15, marginLeft: 100}}>
                  <button type="submit" className="btn btn-solid"  disabled={!this.state.isVerify}>check status</button>
                </div>
                </form>
              </div>
              {
                order && <h4 style={{color:  '#444444', fontWeight: 650}}>
                  Hi <b style={{color: '#ff4c3b'}}>{order.userName}</b> your order status is <b style={{color: '#ff4c3b'}}>{order.orderStatus}</b>
                </h4>
              }
            </div>
          </div>
        </section>
      </div>
    )
  }
}

export default OrderTracking;