import React, {Component} from 'react';
import { baseUrl} from '../../baseUrl'

class orderSuccess extends Component {

    render (){
        !this.props.location.state && this.props.history.push('/')
        const {order, orderId , symbol} = this.props.location.state;

        var options = { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' };
        var current = new Date();
        var next5days = new Date(Date.now() + 5 * 86400000);
        let CheckDate = current.toLocaleDateString("en-US", options).toString()
        let deliveryDate = next5days.toLocaleDateString("en-US", options).toString()

        return (
            <div>
                <section className="section-b-space light-layout">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="success-text">
                                    <img src="assets/images/email-temp/delivery.png" alt="" style={{marginBottom: 30}} /> <br />
                                    <img src="assets/images/email-temp/success.png" alt="" />
                                    <h2>thank you <b style={{display: "inline", color: '#4EAD4E'}}>{order.userName}</b></h2>
                                    <p>Order Has Been Placed Successfully</p>
                                    <p>Order ID : <b style={{display: "inline", color: '#4EAD4E', fontWeight: "bold"}}>{orderId}</b></p>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

                <section className="section-b-space">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-6">
                                <div className="product-order">
                                    <h3>your order details</h3>
                                    {order.products.map((item, index) => {
                                    return <div className="row product-order-detail" key={index}>
                                                <div className="col-3">
                                                    <img src={`${baseUrl}api/uploads/${item.image}`} alt="" className="img-fluid" />
                                                </div>
                                                <div className="col-3 order_detail">
                                                    <div>
                                                        <h4>product name</h4>
                                                        <h5>{item.title}</h5>
                                                    </div>
                                                </div>
                                                <div className="col-3 order_detail">
                                                    <div>
                                                        <h4>quantity</h4>
                                                        <h5>{item.qty}</h5>
                                                    </div>
                                                </div>
                                                <div className="col-3 order_detail">
                                                    <div>
                                                        <h4>price</h4>
                                                        <h5>{symbol}{item.sum}</h5>
                                                    </div>
                                                </div>
                                            </div>
                                    })}
                                    <div className="total-sec">
                                        <ul>
                                            <li>subtotal <span>{symbol}{order.totalAmount}</span></li>
                                            {/* <li>shipping <span>$0</span></li> */}
                                            {/* <li>tax(GST) <span>$0</span></li> */}
                                        </ul>
                                    </div>
                                    <div className="final-total">
                                        <h3>total <span>{symbol}{order.totalAmount}</span></h3>
                                    </div>
                                </div>
                            </div>
                            <div className="col-lg-6">
                                <div className="row order-success-sec">
                                    <div className="col-sm-6">
                                        <h4>summery</h4>
                                        <ul className="order-detail">
                                            <li>Order Date: {CheckDate}</li>
                                            <li>Order Total: {symbol}{order.totalAmount}</li>
                                        </ul>
                                    </div>
                                    <div className="col-sm-6">
                                        <h4>shipping address</h4>
                                        <ul className="order-detail">
                                            <li>{order.completeShippingAddress}</li>
                                            
                                        </ul>
                                    </div>
                                    <br />
                                    <div className="col-sm-12 payment-mode" style={{marginTop: 20}}>
                                        <h4>payment method</h4>
                                        <p>Pay on Delivery (Cash/Card). Cash on delivery (COD) available. </p>
                                    </div>
                                    <div className="col-md-12">
                                        <div className="delivery-sec">
                                            <h3>expected date of delivery</h3>
                                            <h2>{deliveryDate}</h2>
                                        </div>
                                    </div>
                                </div>
                            </div>

                        </div>
                    </div>
                </section>
            </div>
           
        )
    }
}

export default orderSuccess