import React, {Component} from 'react';
import {Helmet} from 'react-helmet'
import { connect } from 'react-redux'
import SimpleReactValidator from 'simple-react-validator';
import { baseUrl } from '../../baseUrl';
import axios from 'axios';
import { toast  } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';

import Breadcrumb from "../common/breadcrumb";
import {getCartTotal} from "../../services";
import {emptyCart} from "../../actions"
import './checkout.css';
var Recaptcha = require('react-recaptcha');

class checkOut extends Component {

    constructor (props) {
        super (props)

        this.state = {
            payment:'CASH ON DELIVERY',
            userName:'Abdul Rehman',
            userPhone:'03219538923',
            userEmail:'ar@gamil.com',
            completeShippingAddress:'House no. 111 Ali park extension',
            city:'Lahore',
            shipmentNote: '',
            show: false,
            isVerify: false
        }
        this.validator = new SimpleReactValidator();
    }

    setStateFromInput = (event) => {
        var obj = {};
        obj[event.target.name] = event.target.value;
        this.setState(obj);
    }

    handleSubmit= async (event) => {
       event.preventDefault();
       
        const newCheckout = {
            userName: this.state.userName,
            userPhone: this.state.userPhone,
            city: this.state.city,
            completeShippingAddress: this.state.completeShippingAddress,
            products: this.props.cartItems,
            totalAmount: this.props.total,
            shipmentNote: this.state.shipmentNote,
            paymentMethod: this.state.payment,
            orderStatus: "IN PROGRESS"
        }

        const orderUrl = baseUrl + 'api/orders';
        await axios.post(orderUrl, newCheckout)
        .then(res => {
            toast.success("Order has been placed successfully!")
            this.props.emptyCart();
            this.props.history.push({
                pathname: '/order-success',
                state: {
                    order: newCheckout,
                    symbol: this.props.symbol,
                    orderId: res.data.orderId 
                }
            })
        }).catch(err => {
            toast.error("Failed to place order. Try agin!", err)
        })
    }

    checkhandle(value) {
        this.setState({
            payment: value
        })
    }

    handleHideDiv = () =>{
        this.setState({
            show:!this.state.show
        })
    }

    recaptchaLoaded = () => {
        console.log("captcha successfully loaded!")
       }
     
       verifyCallback = (res) => {
         if(res) {
           this.setState({isVerify: true})
         }
       }
    
    
    render (){
        const {cartItems, symbol, total} = this.props;

        return (
            <div>

                {/*SEO Support*/}
                <Helmet>
                    <title>Nai-Soch | CheckOut Page</title>
                    <meta name="description" content="Multikart  Multipurpose eCommerce React Template is a multi-use React template. It is designed to go well with multi-purpose websites. Multikart Bootstrap 4 Template will help you run multiple businesses." />
                </Helmet>
                {/*SEO Support End */}

                <Breadcrumb  title={'Checkout'}/>

                <section className="section-b-space">
                    <div className="container padding-cls">
                        <div className="checkout-page">
                            <div className="checkout-form">
                                <form onSubmit={this.handleSubmit}>
                                    <div className="checkout row">
                                        <div className="col-lg-6 col-sm-12 col-xs-12">
                                            <div className="checkout-title">
                                                <h3>Personal Information</h3>
                                            </div>
                                            <div className="row check-out">
                                                <div className="form-group col-md-12 col-sm-12 col-xs-12">
                                                    <div className="field-label required">Full Name</div>
                                                    <input type="text" name="userName" value={this.state.userName} onChange={this.setStateFromInput} placeholder="Enter your name" />
                                                    {this.validator.message('userName', this.state.userName, 'required|alpha_space')}
                                                </div>
                                                <div className="form-group col-md-12 col-sm-12 col-xs-12">
                                                    <div className="field-label required">Phone</div>
                                                    <input type="text" name="userPhone"  value={this.state.userPhone} onChange={this.setStateFromInput} placeholder="Enter your phone number" />
                                                    {this.validator.message('userPhone', this.state.userPhone, 'required|phone')}
                                                </div>
                                               
                                            </div>
                                            <div className="checkout-title">
                                                <h3>Shipping Information</h3>
                                            </div>
                                            <div className="row check-out">
                                            <div className="form-group col-md-12 col-sm-12 col-xs-12">
                                                <div className="field-label required">City/Tehseel/District</div>
                                                <input type="text" name="city" value={this.state.city} onChange={this.setStateFromInput} placeholder="Enter your city/tehseel/district" />
                                                {this.validator.message('city', this.state.city, 'required|alpha_space')}
                                            </div>
                                            
                                            <div className="form-group col-md-12 col-sm-12 col-xs-12">
                                                    <div className="field-label required">Complete Shipping Address</div>
                                                    <input type="text" name="completeShippingAddress" value={this.state.completeShippingAddress} onChange={this.setStateFromInput} placeholder="Street address" />
                                                    {this.validator.message('completeShippingAddress', this.state.completeShippingAddress, 'required|alpha_num_dash_space|max:120')}
                                            </div>
                                            <div className="form-group col-md-12 col-sm-12 col-xs-12">
                                                    <div className="field-label">Shipment Notes(if any..)</div>
                                                    <textarea type="text" name="shipmentNote" value={this.state.shipmentNote} onChange={this.setStateFromInput} rows="8" style={{height: 70}} />
                                            </div>
                                                
                                            {/* remaining */}
                                                
                                            </div>
                                        </div>
                                        <div className="col-lg-6 col-sm-12 col-xs-12">
                                            <div className="checkout-details">
                                                <div className="order-box">
                                                    <div className="title-box">
                                                        <div>Product <span> Total</span></div>
                                                    </div>
                                                    <ul className="qty">
                                                        {cartItems.map((item, index) => {
                                                            return <li key={index}><img src={`${baseUrl}api/uploads/${item.image}`} width={40} height={40} style={{marginRight: 10}} alt="checkout book pic" />
                                                            {item.title}  {item.qty} <span>{symbol} {item.sum}</span></li> })
                                                        }
                                                    </ul>
                                                    <ul className="sub-total">
                                                        <li>Subtotal <span className="count">{symbol}{total}</span></li>
                                                        {/* <li>Shipment Charges <span className="count" style={{color: '#333333'}}>{symbol}50</span></li> */}
                                                    </ul>

                                                    <ul className="total">
                                                        <li>Total <span className="count">{symbol}{total}</span></li>
                                                    </ul>
                                                </div>

                                                <div className="payment-box">
                                                    <div><h3 style={{color: '#444444', fontSize: 22, fontWeight: '600'}}>Payment</h3></div>
                                                    <div className="upper-box">
                                                        <div className="payment-options">
                                                            <ul>
                                                                <li>
                                                                    <div className="radio-option stripe">
                                                                        <input type="radio" name="payment-group" id="payment-2" defaultChecked={true} onClick={() => this.checkhandle('CASH ON DELIVERY')} />
                                                                        <label htmlFor="payment-2">Cash on Delivery</label>
                                                                    </div>
                                                                </li>
                                                                <li>
                                                                    <div className="radio-option stripe">
                                                                        <input type="radio" name="payment-group" id="payment-3" onClick={() => this.checkhandle('bankTransfer'), this.handleHideDiv} />
                                                                        <label htmlFor="payment-3">Direct Bank Transfer</label>
                                                                        {
                                                                            this.state.show ?
                                                                            <div className="text-justify" style={{padding: 20, color: '#333333', fontWeight: 'bold', letterSpacing: 0}}>
                                                                                Make your payment directly into our bank account. Please use your Order ID as the payment reference. 
                                                                                Your order will not be shipped until the funds have cleared in our account.<br/>
                                                                                Bank Name: HBL <br/>
                                                                                Account Title: Nai-Soch <br/>
                                                                                Account number: 0832934081009595
                                                                            </div> : null
                                                                        }
                                                                        
                                                                    </div>
                                                                </li>
                                                            </ul>
                                                        </div>
                                                    </div>
                                                    {(total !== 0)?
                                                    <div className="text-right">
                                                    <Recaptcha
                                                        sitekey="6LdWB9wZAAAAAEF1-7VhB9xl028ZK4uuURBHLBQB"
                                                        render="explicit"
                                                        onloadCallback={this.recaptchaLoaded}
                                                        verifyCallback={this.verifyCallback}
                                                    />
                                                        {(this.state.payment === 'CASH ON DELIVERY')? 
                                                            <button type="submit" className="btn-solid btn" disabled={!this.state.isVerify}>Place Order</button>: ''
                                                         }
                                                    </div>
                                                    : ''}
                                                    <div className="text-justify" style={{color: '#333333', fontWeight: 'bold', letterSpacing: 0, marginTop: 20}}>NOTE: Your personal data will be used to process your order, support your experience throughout this website, and for other purposes described in our privacy policy.</div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
const mapStateToProps = (state) => ({
    cartItems: state.cartList.cart,
    symbol: state.data.symbol,
    total: getCartTotal(state.cartList.cart)
})

export default connect(
    mapStateToProps,
    {emptyCart}
)(checkOut)