// Get Unique author from Database
export const getUniqueAuthors = (products) => {
    // console.log("PRODUCTS" , products)
    var uniqueAuthors = [];
    products.map((product, index) => {
        if (product.authorsIds) {
            product.authorsIds.map((auth) => {
                if (uniqueAuthors.indexOf(auth.name) === -1) {
                    uniqueAuthors.push(auth.name);
                    // console.log("Names", auth.name)
                }
            })
        }
    })
    // console.log("AUTHORS",uniqueAuthors)
    return uniqueAuthors;
}

// Get Unique category from Database
export const getUniqueCategory = (products) => {
    var uniqueCategory = [];
    products.map((product, index) => {
        if (product.category) {
            if (uniqueCategory.indexOf(product.category) === -1){
                uniqueCategory.push(product.category);
            }
        }
    })
    return uniqueCategory;
}

// Get Minimum and Maximum Prices from Json Data
export const getMinMaxPrice = (products) => {
    let min = 100, max = 3000;

    products.map((product, index) => {
        let v = product.price;
        min = (v < min) ? v : min;
        max = (v > max) ? v : max;
    })

    return {'min':min, 'max':max};
}

export const getVisibleproducts = (data, { author, category, value, sortBy }) => {  
    // console.log("COLLECTION DATA=> ",data.products)
    if(author.length ===0 &&
        category.length === 0 &&
       value.min=== 250&&value.max=== 1400 &&
        sortBy === "" ){
            return data.products;
        }
        
    const authorAndPriceMatch = data.products.filter(product => {
        let authorMatch;
        if(author.length!==0 && product.authorsIds)
            authorMatch = product.authorsIds.some(auth => author.includes(auth.name)) && (product.offer === false)
        else
            authorMatch = true;

        let categoryMatch;
        if(category.length!==0 && product.category){
            categoryMatch = product.category.includes(category) && (product.offer === false)
        }
        else
        categoryMatch = true;

        // let reg = new RegExp(searchString, "i");    
        // let searchItems;
        // if(searchString){
        //     const b = reg.test(product.title) || reg.test(product.category);
        //     const author = reg.test(product.authorsIds.map(auth => auth.name))
        //     return b || author
        // }
        
        const startPriceMatch = typeof value.min !== 'number' || value.min <= product.price;
        const endPriceMatch = typeof value.max !== 'number' || product.price <= value.max;
        return authorMatch && categoryMatch && startPriceMatch && endPriceMatch
        //  || searchItems;
    })

    const  authorAndPriceMatchSorted = authorAndPriceMatch.sort((product1, product2) => {
        if (sortBy === 'HighToLow') {
            return product2.price < product1.price ? -1 : 1;
        } else if (sortBy === 'LowToHigh') {
            return product2.price > product1.price ? -1 : 1;
        } else if (sortBy === 'Newest') {
            return product2._id < product1._id ? -1 : 1;
        } else if (sortBy === 'AscOrder') {
            return product1.title.localeCompare(product2.title);
        } else if (sortBy === 'DescOrder') {
            return product2.title.localeCompare(product1.title);
        } else{
            return product2._id > product1._id ? -1 : 1;
        }
    });

    return authorAndPriceMatchSorted;
}

export const getCartTotal = cartItems => {
    var total = 0;
    for(var i=0; i<cartItems.length; i++){
        total += parseInt(cartItems[i].qty)*parseInt(cartItems[i].price);
    }
    return total;
}

// searching the book
export const getSearchedBooks = (products, searchString) => {
    let reg = new RegExp(searchString, "i");    
    const items = products.filter(product => {
        const b = reg.test(product.title) || reg.test(product.category);
        const author = reg.test(product.authorsIds.map(auth => auth.name))
        return b || author
    })
    console.log("SearchItems", items)
    return items
}

