// Get New Products
export const getNewBooks = (products) => {
    const items = products.filter(product => {
        return product.new === true;
    })
    return items
}

// get offers
export const getBookOffers = (products) => {
    const items = products.filter(product => product.offer === true )
    return items;
}

// get premium books
export const getPremiumBooks = (products) => {
    const items = products.filter(product => product.premium === true )
    return items;
}

// Get Related Items
export const getRelatedItems = (products, category, authorName) => {
    const items = products.filter(product => {
        return product.category === category && product.authorsIds.some(auth => authorName.includes(auth.name)) && product.offer === false;
    })
    return items

}

// Get Best Seller
export const getBestSellingBooks = (products) => {
    const items = products.filter(product => {
        return product.sale === true;
    })
    return items
}

// get Unique author Books
export const getAuthorBooks = (products, authorName) => {
    const items = products.filter(product => {
        return product.authorsIds.some(auth => authorName.includes(auth.name)) && product.offer === false;
    })
    return items
}
