import React, {Component} from 'react';
import Breadcrumb from "../common/breadcrumb";
import axios from 'axios';
import { baseUrl } from '../../baseUrl';
import { toast  } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.min.css';

class Contact extends Component {
    constructor() {
        super();
        this.state= {
            name: '',
            email: '',
            phone: '',
            message:''
        }
    }

    setStateFromInput = (event) => {
        var obj = {};
        obj[event.target.name] = event.target.value;
        this.setState(obj);
    }

    handleSubmit = async (e) => {
        e.preventDefault();
        
        const obj = {
            email: this.state.email,
            name: this.state.name,
            message: this.state.message,
            phone: this.props.phone
        }

        const url = baseUrl + 'api/contact';
        await axios.post(url, obj)
        .then(res => {
            toast.success("Thank you for your feedback!")
        }).catch(err => {
            toast.error("Message sending fail. Try agin!")
        })
    }

    render (){
        return (
            <div>
                <Breadcrumb title={'Contact Us'}/>
                {/*Forget Password section*/}
                <section className=" contact-page section-b-space">
                    <div className="container">
                        <div className="row section-b-space">
                            <div className="col-lg-7 map">
                                <iframe
                                    title="Address"
                                    src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3399.063500373577!2d74.30639911515198!3d31.577306481350835!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x39191ca1048323dd%3A0x389a1cfd75122bd9!2sHadia%20Haleema%20Center%2C%20Circular%20Rd%2C%20Anarkali%20Bazaar%20Lahore%2C%20Punjab%2054000!5e0!3m2!1sen!2s!4v1603389133049!5m2!1sen!2s"
                                    allowFullScreen></iframe>
                            </div>
                            <div className="col-lg-5">
                                <div className="contact-right">
                                    <ul>
                                        <li>
                                            <div className="contact-icon">
                                                <img src={`${process.env.PUBLIC_URL}/assets/images/icon/phone.png`} alt="phone icon" />
                                                    <h6>Contact Us</h6>
                                            </div>
                                            <div className="media-body">
                                                <p>+92-300-4495102</p>
                                                <p>+92-300-4495102</p>
                                            </div>
                                        </li>
                                        <li>
                                            <div className="contact-icon">
                                                <i className="fa fa-map-marker" aria-hidden="true"></i>
                                                <h6>Address</h6>
                                            </div>
                                            <div className="media-body">
                                                <p>Office # 46-47 first floor hadia haleema center
                                                    ghazni street urdu bazar Lahore, Pakistan</p>
                                            </div>
                                        </li>
                                        <li>
                                            <div className="contact-icon">
                                                <img src={`${process.env.PUBLIC_URL}/assets/images/icon/email.png`} alt="email icon" />
                                                    <h6>Address</h6>
                                            </div>
                                            <div className="media-body">
                                                <p>Support@naisoch.com</p>
                                                <p>info@naisoch.com</p>
                                            </div>
                                        </li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="row">
                            <div className="col-sm-12">
                                <form className="theme-form" onSubmit={this.handleSubmit}>
                                    <div className="form-row">
                                        <div className="col-md-4">
                                            <label htmlFor="name">Your Name</label>
                                            <input type="text" className="form-control" id="name"
                                                   placeholder="Enter Your name" required=""
                                                   name="name" onChange={this.setStateFromInput}
                                                value={this.state.name}
                                             />
                                        </div>
                                        <div className="col-md-4">
                                            <label htmlFor="review">Phone number</label>
                                            <input type="text" className="form-control" id="review"
                                                   placeholder="Enter your number" required="" 
                                                   name="phone" onChange={this.setStateFromInput}
                                                    value={this.state.phone}
                                                   />
                                        </div>
                                        <div className="col-md-4">
                                            <label htmlFor="email">Email</label>
                                            <input type="email" className="form-control" id="email" placeholder="Email"
                                                   required="" 
                                                   name="email" onChange={this.setStateFromInput}
                                                    value={this.state.email}
                                                   />
                                        </div>
                                        <div className="col-md-12">
                                            <label htmlFor="review">Write Your Message</label>
                                            <textarea className="form-control" placeholder="Write Your Message"
                                                      id="exampleFormControlTextarea1" rows="6"
                                                      name="message" onChange={this.setStateFromInput}
                                                        value={this.state.message}
                                                      ></textarea>
                                        </div>
                                        <div className="col-md-12">
                                            <button className="btn btn-solid" type="submit">Send Your Message</button>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </section>

            </div>
        )
    }
}

export default Contact