import React, {Component} from 'react';
import { connect } from 'react-redux'
import InputRange from 'react-input-range';
import 'react-input-range/lib/css/index.css';
import { SlideToggle } from 'react-slide-toggle';

import {getMinMaxPrice, getUniqueAuthors, getUniqueCategory } from '../../../services';
import {filterPrice, filterAuthor, filterCategory } from '../../../actions'

class NaisochFilter extends Component {
    constructor(props) {
        super(props);

        this.state = {
            openFilter: false
        }
    }

    closeFilter = () => {
        document.querySelector(".collection-filter").style = "left: -365px";
    }

    clickAuthorHendle(event, authors) {
        var index = authors.indexOf(event.target.value);
        if (event.target.checked){
            authors.push(event.target.value); // push in array checked value
        }
        else
            authors.splice(index, 1); // removed in array unchecked value

        this.props.filterAuthor(authors);
    }

    clickCategoryHendle(event, categories) {
        var index = categories.indexOf(event.target.value);
        if (event.target.checked){
            categories.push(event.target.value); // push in array checked value
        }
        else
            categories.splice(index, 1); // removed in array unchecked value

        this.props.filterCategory(categories);
    }

    render (){
        const filteredAuthors = this.props.filters.author;
        const filteredCategories = this.props.filters.category;
        return (
                <div className="collection-filter-block">
                    {/*author filter start*/}
                    <div className="collection-mobile-back">
                        <span className="filter-back" onClick={(e) => this.closeFilter(e)} >
                            <i className="fa fa-angle-left" aria-hidden="true"></i> back
                        </span>
                    </div>
                    
                    <SlideToggle>
                        {({onToggle, setCollapsibleElement}) => (
                            <div className="collection-collapse-block">
                                <h3 className="collapse-block-title" onClick={onToggle}>Author</h3>
                                <div className="collection-collapse-block-content"  ref={setCollapsibleElement}>
                                    <div className="collection-brand-filter">
                                        {this.props.authors.map((author, index) => {
                                            return (
                                                <div className="custom-control custom-checkbox collection-filter-checkbox" key={index}>
                                                    <input type="checkbox" 
                                                        onClick={(e) => this.clickAuthorHendle(e, filteredAuthors)} 
                                                        value={author} 
                                                        defaultChecked={filteredAuthors.includes(author)? true : false}  
                                                        className="custom-control-input" id={author} />
                                                    <label className="custom-control-label"
                                                           htmlFor={author}>{author}</label>
                                                </div> )
                                        })}
                                    </div>
                                </div>
                            </div>
                        )}
                    </SlideToggle>

                    {/*category filter start here*/}
                    <SlideToggle>
                        {({onToggle, setCollapsibleElement}) => (
                            <div className="collection-collapse-block">
                                <h3 className="collapse-block-title" onClick={onToggle}>categories</h3>
                                <div className="collection-collapse-block-content" ref={setCollapsibleElement}>
                                    <div className="collection-brand-filter">
                                    {this.props.categories.map((category, index) => {
                                            return (
                                                <div className="custom-control custom-checkbox collection-filter-checkbox" key={index}>
                                                    <input type="checkbox" 
                                                        onClick={(e) => this.clickCategoryHendle(e,filteredCategories)} 
                                                        value={category} 
                                                        defaultChecked={filteredCategories.includes(category)? true : false}  
                                                        className="custom-control-input" 
                                                        id={category} />
                                                    <label className="custom-control-label"
                                                           htmlFor={category}>{category}</label>
                                                </div> )
                                        })}
                                    </div>
                                </div>
                            </div>
                        )}
                    </SlideToggle>
                    {/*price filter start here */}
                    <SlideToggle>
                        {({onToggle, setCollapsibleElement}) => (
                            <div className="collection-collapse-block open">
                                <h3 className="collapse-block-title" onClick={onToggle}>price</h3>
                                <div className="collection-collapse-block-content block-price-content" ref={setCollapsibleElement}>
                                    <div className="collection-brand-filter">
                                        <div className="custom-control custom-checkbox collection-filter-checkbox">
                                            <InputRange
                                                maxValue={this.props.prices.max}
                                                minValue={this.props.prices.min}
                                                value={this.props.filters.value}
                                                onChange={value => this.props.filterPrice({ value })} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )}
                    </SlideToggle>
                </div>
        )
    }
}


const mapStateToProps = state => ({
    authors: getUniqueAuthors(state.data.products),
    categories: getUniqueCategory(state.data.products),
    prices: getMinMaxPrice(state.data.products),
    filters: state.filters
})

export default connect(
    mapStateToProps,
    { filterPrice, filterAuthor, filterCategory }
)(NaisochFilter);

// data in store-->reducer fucntion-->inital state
//mapstatetoprops--> inject state in props in a component