import React, {Component} from 'react';
import { withTranslate } from 'react-redux-multilingual';

class TopBar extends Component {
    
    onLoginClick = () => {
        this.props.history.push("/login")
    }

    onRegisterClick = () => {
        this.props.history.push("/register")
    }
    

    render() {
        const {translate} = this.props;
        
        return (
          <div className="top-header">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6">
                            <div className="header-contact">
                                <ul>
                                    <li>{translate('topbar_title', { theme_name: ' Nai Soch' })}</li>
                                </ul>
                            </div>
                        </div>
                        <div className="col-lg-6 text-right">
                            <ul className="header-dropdown">
                            <a href="tel:+923004495102"><li><i className="fa fa-phone" aria-hidden="true"></i>{translate('call_us')}:  +92-300-4495102</li></a>
                                {/* <li className="mobile-wishlist"><Link to={`${process.env.PUBLIC_URL}/wishlist`}><i className="fa fa-heart" aria-hidden="true"></i>{translate('wishlist')}</Link></li>
                                <li className="onhover-dropdown mobile-account">
                                    <i className="fa fa-user" aria-hidden="true"></i> {translate('my_account')}
                                    <ul className="onhover-show-div">
                                            <li onClick={this.onLoginClick}>Login</li>
                                            <li onClick={this.onRegisterClick}>Register</li>
                                    </ul>
                                </li> */}
                            </ul>
                        </div>
                    </div>
                </div>
            </div>
           
        )
    }
    
}

export default withTranslate(TopBar);