import React from 'react';
import {Link} from 'react-router-dom';
import { baseUrl } from '../../../../baseUrl'

const CartHeader  = ({item, total, symbol, removeFromCart}) => {
        return (
            <li >
                <div className="media">
                    <Link to={'/naisoch/product/' + item._id}><img alt="" className="mr-3" src={`${baseUrl}api/uploads/${item.image}`} /></Link>
                    <div className="media-body">
                        <Link to={'/naisoch/product/' + item._id}><h4>{item.title}</h4></Link>
                        <h4><span>{item.qty} x {symbol} {item.price}</span></h4>
                    </div>
                </div>
                {/*<span>{cart}</span>*/}
                <div className="close-circle">
                    <i className="fa fa-times" aria-hidden="true" onClick={ removeFromCart}></i>
                </div>
            </li>
        )
    }



export default CartHeader;
