import React, { Component } from "react";
import { Link } from "react-router-dom";
import Modal from "react-responsive-modal";
import { baseUrl } from "../../../../baseUrl";
import CartModal from "../../../cart/CartModal.jsx"

class ProductStyleSeven extends Component {
  constructor(props) {
    super(props);

    this.state = {
      open: false,
      stock: "InStock",
      quantity: 1,
      image: "",
      openModal: false
    };
  }

  onOpenModal = () => {
    this.setState({ open: true });
  };

  onCloseModal = () => {
    this.setState({ open: false });
  };

  onOpenCartModal = () => {
    this.setState({ openModal: true });
  };

  onCloseCartModal = () => {
    this.setState({ openModal: false });
  };
  
  minusQty = () => {
    if (this.state.quantity > 1) {
      this.setState({ stock: "InStock" });
      this.setState({ quantity: this.state.quantity - 1 });
    }
  };

  plusQty = () => {
    if (this.props.product.numberInStock >= this.state.quantity) {
      this.setState({ quantity: this.state.quantity + 1 });
    } else {
      this.setState({ stock: "Out of Stock !" });
    }
  };
  changeQty = (e) => {
    this.setState({ quantity: parseInt(e.target.value) });
  };


  render() {
    const {
      product,
      symbol,
      onAddToCartClicked
    } = this.props;
    const { open, openModal } = this.state;

    // let RatingStars = [];
    // for (var i = 0; i < product.rating; i++) {
    //   RatingStars.push(<i className="fa fa-star" key={i} />);
    // }

    return (
      <div className="product-box">
        <div className="img-wrapper">
          {/* <div className="lable-block">
            {product.new == true ? <span className="lable3">new</span> : ""}
            {product.sale == true ? (
              <span className="lable4">on sale</span>
            ) : (
              ""
            )}
          </div> */}
          <div className="front">
            <Link
              to={'/naisoch/product/' + product._id}
            >
              <img
                src={`${baseUrl}api/uploads/${product.image}`}
                className="img-fluid"
                alt=""
              />
            </Link>
          </div>
          <div className="cart-info cart-wrap">
            
            <a
              href="javascript:void(0)"
              data-toggle="modal"
              data-target="#quick-view"
              title="Quick View"
              onClick={this.onOpenModal}
            >
              <i className="fa fa-search" aria-hidden="true" />
            </a>
          </div>
          <div
            className="add-button"
            title="Add to cart"
            onClick={() =>{this.onOpenCartModal(); onAddToCartClicked(product, 1)}}
          >
            Add To Cart
          </div>
        </div>
        <div className="product-detail  text-center">
          <div>
            {/* <div className="rating">{RatingStars}</div> */}
            <Link
              to={'/naisoch/product/' + product._id}
            >
              <h6>{product.title}</h6>
            </Link>
            <h4>
              {symbol}
              {product.price}
            </h4>
          </div>
        </div>
        

        <Modal open={open} onClose={this.onCloseModal} center>
          <div
            className="modal-dialog modal-lg modal-dialog-centered"
            role="document"
          >
            <div className="modal-content quick-view-modal">
              <div className="modal-body">
                <div className="row">
                  <div className="col-lg-6  col-xs-12">
                    <div className="quick-view-img">
                      <img
                        src={`${baseUrl}api/uploads/${product.image}`}
                        alt=""
                        className="img-fluid"
                      />
                    </div>
                  </div>
                  <div className="col-lg-6 rtl-text">
                    <div className="product-right">
                      <h2> {product.title} </h2>
                      <h3>
                        {symbol}
                        {product.price}
                      </h3>
                      
                      <div className="border-product">
                        <h6 className="product-title">product details</h6>
                        <p>{product.description}</p>
                      </div>
                      <div className="product-description border-product">
                        <h6 className="product-title">quantity</h6>
                        <div className="qty-box">
                          <div className="input-group">
                            <span className="input-group-prepend">
                              <button
                                type="button"
                                className="btn quantity-left-minus"
                                onClick={this.minusQty}
                                data-type="minus"
                                data-field=""
                              >
                                <i className="fa fa-angle-left" />
                              </button>
                            </span>
                            <input
                              type="text"
                              name="quantity"
                              value={this.state.quantity}
                              onChange={this.changeQty}
                              className="form-control input-number"
                            />
                            <span className="input-group-prepend">
                              <button
                                type="button"
                                className="btn quantity-right-plus"
                                onClick={this.plusQty}
                                data-type="plus"
                                data-field=""
                              >
                                <i className="fa fa-angle-right" />
                              </button>
                            </span>
                          </div>
                        </div>
                      </div>
                      <div className="product-buttons">
                        <button
                          className="btn btn-solid"
                          onClick={() =>{
                            onAddToCartClicked(product, this.state.quantity)}
                          }
                        >
                          add to cart
                        </button>
                        <Link
                          to={'/naisoch/product/' + product._id}
                          className="btn btn-solid"
                        >
                          view detail
                        </Link>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Modal>
        <CartModal 
          closeCartModal={this.onCloseCartModal}
          openCartModal={this.onOpenCartModal}
          open={openModal}
          addToCartClicked={onAddToCartClicked}
          product={product}
          minusQty={this.minusQty}
          plusQty={this.plusQty}
          quantity={this.state.quantity}
          changeQty={this.changeQty}
        />

        
      </div>
    );
  }
}

export default ProductStyleSeven;
