import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { withTranslate } from 'react-redux-multilingual';
import {SlideUpDown} from "../../../../services/script";
import { getUniqueAuthors, getUniqueCategory} from "../../../../services"
import { connect } from 'react-redux';
import {filterAuthor, filterCategory} from '../../../../actions';

class NavBar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            navClose: { right: '0px' },
            showCheckout: false
        }
    }
    onAboutusClick = () => {
        this.props.history.push("/naisoch/about-us")
    }

    onTrackingClick = () => {
        this.props.history.push("/naisoch/order-tracking")
    }

    onContactClick = () => {
        this.props.history.push("/naisoch/contact")
    }

    componentWillMount() {
        if (window.innerWidth < 750) {
            this.setState({ navClose: { right: '-410px' } })
        }
        if (window.innerWidth < 1199) {
            this.setState({ navClose: { right: '-300px' } })
        }
    }

    openNav() {
        console.log('open')
        this.setState({ navClose: { right: '0px' } })
    }
    closeNav() {
        this.setState({ navClose: { right: '-410px' } })
    }

    onMouseEnterHandler() {
        if (window.innerWidth > 1199) {
            document.querySelector("#main-menu").classList.add("hover-unset");
        }
    }

    handleSubmenu = (event) => {
        if (event.target.classList.contains('sub-arrow'))
            return;

        if(event.target.nextElementSibling.classList.contains('opensubmenu'))
            event.target.nextElementSibling.classList.remove('opensubmenu')
        else{
            document.querySelectorAll('.nav-submenu').forEach(function (value) {
                value.classList.remove('opensubmenu');
            });
            document.querySelector('.mega-menu-container').classList.remove('opensubmenu')
            event.target.nextElementSibling.classList.add('opensubmenu')
        }
    }

    handleMegaSubmenu = (event) => {
        if (event.target.classList.contains('sub-arrow'))
            return;
            
        if(event.target.parentNode.nextElementSibling.classList.contains('opensubmegamenu'))
            event.target.parentNode.nextElementSibling.classList.remove('opensubmegamenu')
        else{
            document.querySelectorAll('.menu-content').forEach(function (value) {
                value.classList.remove('opensubmegamenu');
            });
            event.target.parentNode.nextElementSibling.classList.add('opensubmegamenu')
        }
    }

    clickAuthorHendle(event, authors) {
        authors.pop();
        authors.push(event);

        this.props.filterAuthor(authors);
    }

    clickCategoryHendle(event, categories) {
        
        categories.pop();
        categories.push(event);

        this.props.filterCategory(categories);
    }

    componentDidMount() {
        SlideUpDown('setting-title');
    }

    render() {
        const { translate } = this.props;
        const filteredAuthors = this.props.filters.author;
        const filteredCategories = this.props.filters.category;
        return (
            <div>
                <div className="main-navbar">
                    <div id="mainnav" >
                        <div className="toggle-nav" onClick={this.openNav.bind(this)} >
                            <i className="fa fa-bars sidebar-bar"></i>
                        </div>
                        <ul className="nav-menu" style={this.state.navClose}>
                            <li className="back-btn" onClick={this.closeNav.bind(this)} >
                                <div className="mobile-back text-right">
                                    <span >Back</span>
                                    <i className="fa fa-angle-right pl-2" aria-hidden="true"></i>
                                </div>
                            </li>
                            <li >
                                <Link to='/naisoch/collection' className="nav-link" onClick={(e) => this.handleSubmenu(e)}>
                                    {translate('books')}
                                    <span className="sub-arrow"></span>
                                </Link>
                                <ul className="nav-submenu" >
                                {
                                    this.props.categories.map((category, index) => {
                                        return (
                                            <li key={index} 
                                            value={category}
                                            onClick={(e) => this.clickCategoryHendle(category,filteredCategories)}>
                                                <Link to='/naisoch/collection'>
                                                    {category}
                                                </Link>
                                            </li>
                                        )
                                    })
                                } 
                                </ul>
                            </li>
                            <li >
                                <Link to="#" className="nav-link" onClick={(e) => this.handleSubmenu(e)}>
                                    {translate('authors')}
                                    <span className="sub-arrow"></span>
                                </Link>
                                <ul className="nav-submenu">
                                {
                                    this.props.authors.map((author, index) => {
                                        return (
                                            <li key={index}
                                                value={author}>
                                                <Link to='/naisoch/collection'>
                                                <div onClick={(e) => this.clickAuthorHendle(author,filteredAuthors)}>
                                                    {author }
                                                </div>
                                                </Link>
                                            </li>

                                        )
                                    })
                                }
                                </ul>
                            </li>
                            <li>
                                <Link to='/naisoch/offers' className="nav-link" >
                                    {translate('offers')}
                                    
                                </Link>
                            </li>
                            <li className="mega-menu-container" onClick={this.onAboutusClick} >
                                <Link to='' className="nav-link">
                                    {translate('about_us')}
                                </Link>
                            </li>
                            <li className="mega-menu-container" onClick={this.onContactClick} >
                                <Link to='' className="nav-link">
                                    {translate('contact')}
                                </Link>
                            </li>
                            <li onClick={this.onTrackingClick}>
                                <Link to='' className="nav-link">
                                    {translate('order_tracking')}
                                </Link>
                            </li>
                            
                        </ul>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = state => ({
    authors: getUniqueAuthors(state.data.products),
    categories: getUniqueCategory(state.data.products),
    filters: state.filters,

})


export default withRouter(connect(
    mapStateToProps,
    { filterAuthor, filterCategory }
    )(withTranslate(NavBar)));