import React from 'react';
import ReactDOM from 'react-dom';
import { Provider } from 'react-redux';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { ScrollContext } from 'react-router-scroll-4';
import { IntlProvider } from 'react-redux-multilingual'
import './index.scss';

// Import custom components
import store from './store';
import translations from './constants/translations'
import { getAllProducts } from './actions'


// Layouts
import Naisoch from './components/layouts/nai-soch/main';


//Collection Pages
import CollectionNaiSoch from "./components/collection/collection-naisoch";
import CollectionSearch from "./components/collection/collection-search"
import Offers from './components/collection/offers'

// Product Pages
import NaisochProduct from "./components/products/naisochproduct";

// Features
import Layout from './components/app'
import Cart from './components/cart'
import checkOut from './components/checkout'
import orderSuccess from './components/checkout/success-page'

// Extra Pages
import aboutUs from './components/pages/about-us'
import Contact from './components/pages/contact'
import OrderTracking from './components/pages/order-tracking'
import PrivacyPolicy from './components/pages/privacy-policy'
import ShippingPolicy from './components/pages/shipping-policy'
import ReturnPolicy from './components/pages/return-replacement'

class Root extends React.Component {


    render() {
        store.dispatch(getAllProducts());

        return(
        	<Provider store={store}>
                <IntlProvider translations={translations} locale='en'>
				<BrowserRouter basename={'/'} >
					<ScrollContext>
						<Switch>
                            <Layout>

                                {/*Routes For Layouts*/}
                                <Route exact path="/" component={Naisoch}/>

								{/*Routes For Features (Product Collection) */}
                                <Route exact path="/naisoch/collection" component={CollectionNaiSoch}/>
                                <Route exact path="/naisoch/search" component={CollectionSearch}/>
                                <Route exact path="/naisoch/offers" component={Offers}/>

								{/*Routes For Single Product*/}
                                <Route exact path="/naisoch/product/:id" component={NaisochProduct}/>
                                
								
								{/*Routes For custom Features*/}
								<Route exact path="/cart" component={Cart}/>
								<Route exact path="/checkout" component={checkOut}/>
								<Route exact path="/order-success" component={orderSuccess}/>


								{/*Routes For Extra Pages*/}
                                <Route exact path="/naisoch/about-us" component={aboutUs}/>
                                <Route exact path="/naisoch/contact" component={Contact}/>
                                <Route exact path="/naisoch/order-tracking" component={OrderTracking}/>
                                <Route exact path="/privacy-policy" component={PrivacyPolicy}/>
                                <Route exact path="/shipping-policy" component={ShippingPolicy}/>
                                <Route exact path="/return-and-exchange" component={ReturnPolicy}/>

                            </Layout>
                         </Switch>
					  </ScrollContext>
					</BrowserRouter>
                </IntlProvider>
			</Provider>
    	);
    }
}

ReactDOM.render(<Root />, document.getElementById('root'));


