import React, {Component} from 'react';
import { connect } from 'react-redux'
import {Link, withRouter} from 'react-router-dom';
import { addToCart } from '../../../actions'
import {getSearchedBooks} from '../../../services';
import ProductStyleSeven from '../../features/product/common/product-style-seven'

class SearchListing extends Component {

    render (){
        const {products, addToCart, symbol} = this.props;
        return (
            <div>
                <div className="product-wrapper-grid">
                    <div className="container-fluid">
                        {products.length > 0 ?
                           <div className="row">
                                    { products.map((product, index) =>
                                            <div className={`${this.props.colSize===3?'col-xl-3 col-md-6 col-grid-box':'col-lg-'+this.props.colSize} product-5 game-product product-m no-arrow`} key={index}>
                                                <ProductStyleSeven 
                                                    product={product} symbol={symbol}
                                                    onAddToCartClicked={addToCart} key={index}
                                                />
                                            </div>
                                        )
                                    }
                            </div>
                            :
                            <div className="row">
                                <div className="col-sm-12 text-center section-b-space mt-5 no-found" >
                                    <img src={`${process.env.PUBLIC_URL}/assets/images/empty-search.jpg`} className="img-fluid mb-4" alt="search icon" />
                                    <h3>Sorry! Couldn't find the product you were looking For!!!    </h3>
                                    <p>Please check if you have misspelt something or try searching with other words.</p>
                                    <Link to='/' className="btn btn-solid">continue shopping</Link>
                                </div>
                            </div>
                        }
                    </div>
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => {
  let searchString = ownProps.location.search;
  let str = searchString.slice(searchString.indexOf("=") + 1).replace("%20", ' ');
  console.log("searchString", str)
  return {
    products: getSearchedBooks(state.data.products, str),
    symbol: state.data.symbol,
  }
}

export default withRouter(connect(
    mapStateToProps, {addToCart}
)(SearchListing))