import React, {Component} from 'react';
import { connect } from 'react-redux'
import {Link} from 'react-router-dom'
import { baseUrl } from '../../baseUrl'
import Modal from "react-responsive-modal";

import {removeFromCart, incrementQty, decrementQty} from '../../actions'

class CartModal extends Component {

    constructor(props) {
        super(props);
    
        this.state = {
          stock: "InStock",
          quantity: 1,
        };
    }
      
    minusQty = () => {
        if (this.state.quantity > 1) {
          this.setState({ stock: "InStock" });
          this.setState({ quantity: this.state.quantity - 1 });
        }
      };
    
      plusQty = () => {
        if (this.props.product.numberInStock >= this.state.quantity) {
          this.setState({ quantity: this.state.quantity + 1 });
        } else {
          this.setState({ stock: "Out of Stock !" });
        }
      };
      changeQty = (e) => {
        this.setState({ quantity: parseInt(e.target.value) });
      };

    render (){
        const {open, closeCartModal, openCartModal, product, quantity, minusQty, plusQty, changeQty} = this.props;
        const {symbol} = this.props;
        return (
            <Modal open={open} onClose={closeCartModal} onOpen={openCartModal}>
                <div
                    className="modal-dialog modal-lg modal-dialog-centered"
                    role="document"
                >
                    <div className="modal-content quick-view-modal">
                        <div className="modal-body">
                            <div className="row">
                                <div className="col-lg-6  col-xs-12">
                                    <div className="quick-view-img">
                                        <img
                                            src={`${baseUrl}api/uploads/${product.image}`}
                                            alt=""
                                            className="img-fluid"
                                        />
                                    </div>
                                </div>
                                <div className="col-lg-6 rtl-text">
                                    <div className="product-right">
                                        <h2> {product.title} </h2>
                                        <h3>{symbol}{product.price}</h3>
                                        
                                        <div className="border-product">
                                            <h6 className="product-title">product details</h6>
                                            <p>{product.description}</p>
                                        </div>
                                        <div className="product-description border-product">
                                            <h6 className="product-title">quantity</h6>
                                            <div className="qty-box">
                                                <div className="input-group">
                                                    <span className="input-group-prepend">
                                                    <button
                                                        type="button"
                                                        className="btn quantity-left-minus"
                                                        onClick={() => {this.props.decrementQty(product._id) ; minusQty()}}
                                                        data-type="minus"
                                                        data-field=""
                                                    >
                                                        <i className="fa fa-angle-left" />
                                                    </button>
                                                    </span>
                                                    <input
                                                        type="text"
                                                        name="quantity"
                                                        value={quantity}
                                                        onChange={changeQty}
                                                        className="form-control input-number"
                                                    />
                                                    <span className="input-group-prepend">
                                                    <button
                                                        type="button"
                                                        className="btn quantity-right-plus"
                                                        onClick={() => {this.props.incrementQty(product, 1); plusQty()}}
                                                        data-type="plus"
                                                        data-field=""
                                                    >
                                                        <i className="fa fa-angle-right" />
                                                    </button>
                                                    </span>
                                                    <span><h4 style={{color: '#ff4c3b', margin: '15px 10px'}}>{quantity} x {symbol}{product.price}: {symbol}{quantity * product.price}</h4></span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="product-buttons">
                                            <Link
                                                to='/cart'
                                                className="btn btn-solid"
                                            >
                                                view cart
                                            </Link>
                                            <Link
                                                to='/checkout'
                                                className="btn btn-solid"
                                            >
                                            check out
                                            </Link>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </Modal>
        )
    }
}
const mapStateToProps = (state) => ({
    symbol: state.data.symbol,
})

export default connect(
    mapStateToProps,
    {removeFromCart, incrementQty, decrementQty}
)(CartModal)