import React, {Component} from 'react';
import Slider from 'react-slick';
import {connect} from 'react-redux';

import {getRelatedItems} from "../../services";
import {addToCart} from "../../actions";
import ProductStyleSeven from '../features/product/common/product-style-seven'
import { Product5 } from "../../services/script";


class RelatedProduct extends Component {
    render (){
        const {items, symbol, addToCart} = this.props;

        return (
            <section className="section-b-space">
                <div className="container">
                    <div className="row">
                        <div className="col-12 product-related">
                            <h2>related products</h2>
                        </div>
                    </div>
                    <div className="col">
                        <Slider {...Product5} className="slide-1 home-slider">
                            { items.map((product, index) =>
                                    <div className="col-xl-3 col-md-6 col-grid-box game-product" key={index}>
                                    <ProductStyleSeven product={product} symbol={symbol}
                                                     onAddToCartClicked={addToCart} key={index}/>
                                    </div>
                            )}
                        </Slider>
                    </div>
                </div>
            </section>
        )
    }
}

function mapStateToProps(state, ownProps) {
    let category = ownProps.category;
    let author = ownProps.author;
    return {
        items: getRelatedItems(state.data.products, category, author),
        symbol: state.data.symbol
    }
}

export default connect(mapStateToProps, {addToCart})(RelatedProduct);
