import React, {Component} from 'react';
import {Helmet} from 'react-helmet'
import Breadcrumb from "../common/breadcrumb";
import OfferListing from "./common/offer-listing";

class Offers extends Component {

    state = {
        layoutColumns:3
    }

    LayoutViewClicked(colums) {
        this.setState({
            layoutColumns:colums
        })
    }
    
    render (){
        return (
            <div>
                {/*SEO Support*/}
                <Helmet>
                    <title>Nai Soch | Collection of Offers</title>
                    <meta name="description" content="Nai Soch Publishers is an E-commerce business. Its an emerging platform to sell the books via Web Application. The ultimate goal is to make all the published works of humankind available to everyone." />
                </Helmet>
                {/*SEO Support End */}

                <Breadcrumb title={'Offers'}/>

                <section className="section-b-space">
                    <div className="collection-wrapper">
                        <div className="container">
                            <div className="row">
                                <div className="collection-content col">
                                    <div className="page-main-content ">
                                        <div className="">
                                            <div className="row">
                                                <div className="col-sm-12">
                                                    <div className="collection-product-wrapper">
                                                        {/*Products Listing Component*/}
                                                        <OfferListing colSize={this.state.layoutColumns}/>

                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>

            </div>
        )
    }
}

export default Offers;