import React, {Component} from 'react';
import '../common/index.scss';
import {connect} from "react-redux";
import { baseUrl} from "../../baseUrl"

// import custom Components
import Service from "./common/service";
import NewProduct from "../common/new-product";
import Breadcrumb from "../common/breadcrumb";
import DetailsWithPrice from "./common/product/details-price";
import DetailsTopTabs from "./common/details-top-tabs";
import { addToCart, addToCartUnsafe} from '../../actions'
import RelatedProduct from "../common/related-product"


class NaisochProduct extends Component {

    constructor() {
        super();
        this.state = {
            nav1: null,
            nav2: null,
        }
    }
    
    componentDidMount() {
        this.setState({
            nav1: this.slider1,
            nav2: this.slider2
        });
    }

    render(){
       
        const {symbol, item, addToCart, addToCartUnsafe} = this.props;

        return (
            <div>
                {/*Section Start*/}
                {(item)?
                <>
                <Breadcrumb title={'Product / ' + item.title} />
                <section className="section-b-space">
                    <div className="collection-wrapper">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-9 col-sm-12 col-xs-12">
                                    <div className="container-fluid">
                                        <div className="row">
                                            <div className="col-xl-12">
                                                <div className="filter-main-btn mb-2">
                                                    <span className="filter-btn">
                                                        <i className="fa fa-filter" aria-hidden="true"></i> filter</span>
                                                </div>
                                            </div>
                                        </div>
                                        <div className="row">
                                            <div className="col-lg-6 product-thumbnail">
                                            <div>
                                            <img src={`${baseUrl}api/uploads/${item.image}`}
                                                 className="img-fluid  bg-img" alt="" />
                                        </div>
                                            </div>
                                            <DetailsWithPrice symbol={symbol} item={item} navOne={this.state.nav1} addToCartClicked={addToCart} BuynowClicked={addToCartUnsafe} />
                                        </div>
                                    </div>
                                    <DetailsTopTabs item={item} />
                                </div>
                                <div className="col-sm-3 collection-filter">
                                    {/* <BrandBlock/> */}
                                    <Service/>
                                    {/*side-bar single product slider start*/}
                                    <NewProduct/>
                                    {/*side-bar single product slider end*/}
                                </div>
                            </div>
                            <RelatedProduct 
                                category={item.category}
                                author={item.authorsIds.map(auth=> auth.name)}

                                />
                        </div>
                    </div>
                </section>
                </>
                 : ''}
                {/*Section End*/}

            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    let productId = ownProps.match.params.id;
    let product = state.data.products.find(el => el._id === productId);
    return {
        item: product,
        symbol: state.data.symbol
    }
}

export default connect(mapStateToProps, {addToCart, addToCartUnsafe}) (NaisochProduct);