import React, { Component } from "react";

class ShippingPolicy extends Component {
  render() {
    return (
      <div className="policycontainer">
        <h2 style={{ textAlign: "center", paddingBottom: 30 }}>
          Shipping Policy
        </h2>

        <div className="row">
          <div className="col">
            <h3>What are the shipping charges?</h3>
            <p>
              Naisoch Publishers is providing free shipping to most of the areas
              of Pakistan but it may includes shipping charges where we can not
              reach easily.
            </p>
          </div>
        </div>

        <div className="row">
          <div className="col">
            <h3>What is the expected delivery time?</h3>
            <p>
              Once we have called your registered mobile number and confirmed
              the order with you, we expect delivery to happen within 5 to 7
              working days.
            </p>
            <p>
              Delivery timelines vary depending on your city or due to
              unforeseen circumstances.
            </p>
          </div>
        </div>

        <div className="row">
          <div className="col">
            <h3>Is international delivery available?</h3>
            <p>
              International Deliveries are not available yet . It may enable in
              future.
            </p>
          </div>
        </div>

        <div className="row">
          <div className="col">
            <h3>
              I found the package open and the product seal broken on delivery.
              What should i do?
            </h3>
            <p>
              You should refuse to accept an open package. However, if you do
              receive any such package which is open, severely damaged or
              tampered, please get in touch with us immediately via our Help
              Line +92-300-4495102.
            </p>
          </div>
        </div>
      </div>
    );
  }
}

export default ShippingPolicy;
